var locationName;
var locationButtonPrev;
$('.location-switch li').on('click', function () {
    locationName = $('.book-image#' + $(this).attr('id'));
    locationButtonPrev = $(this).siblings();
    $(this).addClass('active');
    $(locationButtonPrev).removeClass('active');
    $('.book-image').addClass('hide');
    $(locationName).removeClass('hide');
});